import { COLORS } from "@/app/constants";
import React from "react";
import { FaCheck } from "react-icons/fa";

// Define the props for the CheckBox component
interface CheckBoxProps {
  isChecked: boolean;
  onClick: () => void;
}

const CheckBox: React.FC<CheckBoxProps> = ({ isChecked, onClick }) => {
  const checkBoxStyle: React.CSSProperties = {
    background: isChecked ? COLORS.BRAND_PRIMARY : "transparent",
    color: isChecked ? "white" : COLORS.BRAND_PRIMARY,
    border: isChecked ? "none" : `1px solid ${COLORS.BRAND_PRIMARY}`,
  };

  return (
    <div
      style={checkBoxStyle}
      className="rounded-sm w-fit p-2 cursor-pointer"
      onClick={onClick}
    >
      <FaCheck />
    </div>
  );
};

export default CheckBox;
